import { useState } from "react";

import * as GridStyle from "~/styles/Grid";
import * as Style from './ItemQuantityStyle';

import ButtonComponent from "~/components/Button/Button"

const ItemQuantity = () => {
  const [countItem, setCountItem] = useState(0);

  const decreaseItem = () => {
    console.log(countItem);
    const value = countItem - 1;
    setCountItem(value);
  }

  const incrementItem = () => {
    console.log(countItem);
    const value = countItem - 2;
    setCountItem(value);
  }

  return (
    <>
      <Style.Quantity>
        <GridStyle.Row>
          <GridStyle.Col
            general={6}
            className="no-margin"
          >
            <Style.Count className="flex align-itcenter">
              <span className="remove" onClick={decreaseItem}>-</span>
              <input type="text" defaultValue={countItem} placeholder="0" />
              <span className="add" onClick={incrementItem}>+</span>
            </Style.Count>
          </GridStyle.Col>
          
          <GridStyle.Col
            general={6}
            className="no-margin"
          >
            <ButtonComponent
            txt={'ADD'}
            className="fn-s18px bg-blue-outlined tx-blue br-4px 30px w-100 fn-wb"
            />
          </GridStyle.Col>
        </GridStyle.Row>
      </Style.Quantity>
    </>
  )
};

export default ItemQuantity;